@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.pixel-font {
    font-family: 'Press Start 2P', monospace;
    text-transform: uppercase;
}

.loading-screen {
    background-color: black;
    color: #00ff88;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    animation: fadeOut 0.5s ease-out forwards;
    animation-delay: 1.8s;
}

.progress-bar {
    width: 200px;
    height: 10px;
    border: 2px solid #00ff88;
    margin-top: 10px;
    position: relative;
    overflow: hidden;
}

.bar-fill {
    background: #00ff88;
    width: 0%;
    height: 100%;
    animation: loadBar 2s ease-in-out forwards;
}

@keyframes loadBar {
    0% {
        width: 0%;
    }

    100% {
        width: 100%;
    }
}

@keyframes fadeOut {
    to {
        opacity: 0;
        visibility: hidden;
    }
}

.loading-screen {
    background-color: black;
    color: #00ff88;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
