}

.progress-bar {
    width: 200px;
    height: 10px;
    border: 2px solid #00ff88;
    margin-top: 10px;
    position: relative;
    overflow: hidden;
}

.bar-fill {
    background: #00ff88;
    width: 0%;
    height: 100%;
    animation: loadBar 2s ease-in-out forwards;
}

@keyframes loadBar {
    0% {
        width: 0%;
    }

    100% {
        width: 100%;
    }
}

.crt-screen {
    position: relative;
    background-color: black;
    color: #00ff88;
    font-family: 'Press Start 2P', monospace;
    overflow: hidden;
}

    /* Horizontal scanlines */
    .crt-screen::before {
        content: '';
        pointer-events: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: repeating-linear-gradient( to bottom, rgba(0, 0, 0, 0) 0px, rgba(0, 0, 0, 0) 1px, rgba(0, 0, 0, 0.05) 2px );
        z-index: 5;
    }

    /* Glow around text */
    .crt-screen * {
        text-shadow: 0 0 2px #00ff88, 0 0 5px #00ff88, 0 0 10px #00ff88;
    }

/* Flicker animation */
@keyframes flicker {
    0%, 100% {
        opacity: 1;
    }

    50% {
        opacity: 0.98;
    }

    75% {
        opacity: 0.95;
    }
}

.crt-screen {
    animation: flicker 1s infinite;
}

.projects {
    padding: 4rem 2rem;
    background-color: #0f0f1a;
    color: #fff;
    text-align: center;
}

.section-title {
    font-size: 2rem;
    margin-bottom: 2rem;
}

.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
    justify-items: center;
    align-items: stretch;
}

.project-card {
    background: #1a1a2e;
    border-radius: 12px;
    padding: 1rem;
    width: 100%;
    max-width: 340px;
    box-shadow: 0 0 10px rgba(122, 92, 255, 0.3);
    transition: transform 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
}

    .project-card:hover {
        transform: scale(1.03);
    }

    .project-card img {
        width: 100%;
        height: 200px;
        object-fit: cover;
        border-radius: 8px;
        margin-bottom: 1rem;
    }

    .project-card h3 {
        margin: 0.5rem 0;
        color: #7a5cff;
    }

    .project-card p {
        color: #ccc;
        font-size: 0.95rem;
        margin-bottom: 1rem;
    }

.view-button {
    display: inline-block;
    padding: 0.5rem 1rem;
    background-color: #7a5cff;
    color: white;
    text-decoration: none;
    border-radius: 6px;
    transition: background-color 0.3s ease;
}

    .view-button:hover {
        background-color: #a78bfa;
    }


.hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 4rem 1rem 2rem;
    text-align: center;
    background-color: #0f0f1a;
    color: #ffffff;
}

.hero-image {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    border: 3px solid #7a5cff;
    margin-bottom: 1rem;
}

.hero-text h1 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
}

.hero-text .highlight {
    color: #7a5cff;
}

.hero-text p {
    font-size: 1.1rem;
    color: #ccc;
    max-width: 600px;
    margin: 0 auto 1rem;
}

.social-links {
    display: flex;
    gap: 1.5rem;
    justify-content: center;
    margin-top: 1rem;
    font-size: 1.5rem;
}

    .social-links a {
        color: #7a5cff;
        transition: 0.3s ease;
    }

        .social-links a:hover {
            color: #ffffff;
        }